.sidebar-row  {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: grab;
    color: #D9DBE0;
    background: #17191A;
}

.sidebar-row:hover  {
    background-color: #595959;
    color: #f5f5f5;
    border-radius: 15px;
}

.sidebar-row > h3  {
    margin-left: 20px;
    font-weight: 600;
    text-transform: capitalize;
}