  
.message-sender {
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: #242627;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
    width: 100%;
  }
  

  input::placeholder  {
      color: #686A6B;
      font-size: 17px;
      font-weight: 700;
 }

  .messagesender-option {
    display: flex;
    flex: 1;
    border-bottom: 1px solid #000000;
    padding: 15px;
  }

  
.messagesender-top > form {
    flex: 1;
    display: flex;
    margin: 10px 5px;
  }
  form > img  {
      width: 55px;
      height: 55px;
      border-radius: 999px;
  }
  
  .messagesender-top > form > input {
    outline-width: 0;
    border: none;
    padding: 15px 15px;
    margin: 2px 10px;
    border-radius: 999px;
    background-color: #3A3C3D;
  }
  
  .messagesender-top > form > button {
    display: none;
  }
  
  .messagesender-input {
    flex: 1;
  }
  
  .messagesender-bottom {
    display: flex;
    justify-content: space-evenly;
  }
  
  .messagesender-option {
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
  }
  
  .messagesender-option > h3 {
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
  }
  
  .messagesender-option:hover {
    background-color: #3A3C3D;
    border-radius: 20px;
  }
  
  .border  {
      border: 1px solid #b8b8b8;
      margin: 8px 15px;
  }