.header  {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    position: sticky;
    background: #242627;
    z-index: 100;
    color: #ffffff;
    box-shadow: 0px 5px  12px -9px #000;
}

.header-left  {
    display: flex;
    justify-content: space-evenly;
}

.header-input {
    display: flex;
    align-items: center;
    background-color: #3c3c3c;
    padding: 5px 10px;
    margin-left: 10px;
    border-radius: 999px;
  }

.header-input > input  {
    border: none;
    background: #383838;
    outline-width: 0;
}
input::placeholder  {
    color: #b5b4b4;
    background-color: #3c3c3c;
    font-size: 14px;
    text-transform: capitalize;
}

.header-left  > img {
    height: 40px;
}

.header-center  {
    display: flex;
    flex: 1;
    justify-content: center;
}

.header-option   {
    display: flex; 
    align-items: center;
    padding: 0 30px;
    cursor: grab;
}

.header-option > .MuiSvgIcon-root  {
    color: #9d9d9d;
}
.header-option:hover > .MuiSvgIcon-root  {
    color: #ffffff;
}

.header-option:hover   {
    background-color: #494949;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}

.header-option-active > .MuiSvgIcon-root {
    color: #0059ff;
}
.header-option-active {
    border-bottom: 3px solid #0059ff;
}

.header-info h4  {
    margin-left: -10px;
}

.header-right {
    display: flex;
}