.post {
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: #242627;
    color: #ACADB0;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  }

  .post-top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
  }

  .post-topInfo > p {
      color: #747678;
  }

  .post-topInfo > h3 {
      font-size: medium;
  }

  
.post-bottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
  }

  .post-image > img {
      width: 100%;
  }

.post-options {
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: #6F7174;
    cursor: grab;
    padding: 15px;
  }
  .post-option {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1;
  }
  
.post-option > p {
    margin-left: 10px;
  }
  
  .post-option:hover {
    background-color: #3A3C3D;
    border-radius: 10px;
  }